<template>
	<div ref="catChart" class="echart-box"></div>
</template>


<script>
	export default {
		name: 'Cat',
		data() {
			return {
				listX: [],
				listY: []
			}
		},
		methods: {
			drawChart() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.catChart);
				if(!chart){
					chart = this.$echarts.init(this.$refs.catChart, 'light');
					this.$once('hook:beforeDestroy',()=>{
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					title: {
						text: '热门商品Top5',
						left: 'center'
					},
					tooltip: {
						trigger: 'item'
					},
					legend: {
						top: 'bottom',
						left: 'center'
					},
					xAxis: {
						name: '商品编号(Id)',
						type: 'category',
						data: this.listX
					},
					yAxis: {
						name: '数据量(data)',
						type: 'value'
					},
					series: [{
						type: 'bar',
						name: '数据量',
						data: this.listY,
						itemStyle: {
							color:  '#83bff6'
						}
					}]
				}
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'data/cat.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					this.listX.push(resp.data[i][0]);
					this.listY.push(resp.data[i][1]);
				}
				this.drawChart();
			})
		}
	}
</script>

